"use client";

import { useEffect, useState } from "react";
import { useToast } from "@/hooks/use-toast";
import { useStudentStore } from "@/store/student.store";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Search, Filter, Download, ChevronLeft, ChevronRight, Eye, Edit, Plus, Trash } from "lucide-react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";

export function AllStudents() {
  const { students, pagination, fetch, add, update, remove, loading } = useStudentStore();
  const { toast } = useToast();

  const [searchQuery, setSearchQuery] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [page, setPage] = useState(1);

  const [addModalOpen, setAddModalOpen] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<any>(null);

  const [formData, setFormData] = useState<any>({
    AdmissionNo: "",
    student_name: "",
    ParentNameFather: "",
    Mother: "",
    address: "",
    OccupationFather: "",
    SchoolClass: "",
    TCNo: "",
    AdmissionDate: "",
    DateOfBirth: "",
    Nationlty: "Indian",
    Relegion: "",
    Caste: "",
    MotherToung: "Hindi",
    ClassStudentAdmitted: "",
    AdharNo: "",
    mobile: "",
    email: "",
  });

  useEffect(() => {
    fetch(page, pagination.limit).catch((err) => toast({ variant: "destructive", title: err.message }));
  }, [page]);

  const filteredStudents = students.filter((student:any) => {
    const matchesSearch =
      student.student_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      student.AdmissionNo.toLowerCase().includes(searchQuery.toLowerCase()) ||
      student.email.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesStatus = statusFilter === "all" || student.status === statusFilter;
    return matchesSearch && matchesStatus;
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleAddStudent = async () => {
    try {
      await add(formData);
      toast({ title: "Student Added Successfully" });
      setAddModalOpen(false);
      setFormData({});
    } catch (err: any) {
      toast({ variant: "destructive", title: err.message || "Failed to add student" });
    }
  };

  const handleEditStudent = async () => {
    try {
      await update({ ...selectedStudent, ...formData });
      toast({ title: "Student Updated Successfully" });
      setEditModalOpen(false);
      setFormData({});
      setSelectedStudent(null);
    } catch (err: any) {
      toast({ variant: "destructive", title: err.message || "Failed to update student" });
    }
  };

  const handleDelete = async (student: any) => {
    try {
      await remove(student);
      toast({ title: student.status === "duplicate" ? "Duplicate deleted" : "Student deleted" });
    } catch (err: any) {
      toast({ variant: "destructive", title: err.message || "Delete failed" });
    }
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-foreground">All Students</h1>
        </div>
        <Button onClick={() => setAddModalOpen(true)}>
          <Plus className="w-4 h-4 mr-2" /> Add Student
        </Button>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4 flex flex-wrap gap-4">
          <div className="relative flex-1 min-w-[200px]">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
            <Input
              placeholder="Search by name, ID, or email..."
              className="pl-10"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Status" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Status</SelectItem>
              <SelectItem value="verified">Verified</SelectItem>
              <SelectItem value="pending">Pending</SelectItem>
              <SelectItem value="duplicate">Duplicate</SelectItem>
            </SelectContent>
          </Select>
        </CardContent>
      </Card>

      {/* Table */}
      <Card>
        <CardHeader>
          <CardTitle>Student Records</CardTitle>
          <CardDescription>
            Showing {filteredStudents.length} of {students.length} students
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="rounded-lg border overflow-hidden">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Admission No</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Father Name</TableHead>
                  <TableHead>Class</TableHead>
                  <TableHead>Mobile</TableHead>
                  <TableHead>Email</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredStudents.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell>{student.AdmissionNo}</TableCell>
                    <TableCell>{student.student_name}</TableCell>
                    <TableCell>{student.ParentNameFather}</TableCell>
                    <TableCell>{student.SchoolClass}</TableCell>
                    <TableCell>{student.mobile}</TableCell>
                    <TableCell>{student.email}</TableCell>
                    <TableCell>{student.status}</TableCell>
                    <TableCell className="text-right flex justify-end gap-2">
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => {
                          setSelectedStudent(student);
                          setFormData(student);
                          setEditModalOpen(true);
                        }}
                      >
                        <Edit className="w-4 h-4" />
                      </Button>
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => handleDelete(student)}
                      >
                        <Trash className="w-4 h-4" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          {/* Pagination */}
          <div className="flex items-center justify-between mt-4">
            <p className="text-sm text-muted-foreground">
              Page {pagination.page} of {Math.ceil(pagination.total / pagination.limit)}
            </p>
            <div className="flex gap-2">
              <Button
                variant="outline"
                size="sm"
                disabled={pagination.page === 1}
                onClick={() => setPage(page - 1)}
              >
                <ChevronLeft className="w-4 h-4 mr-1" /> Previous
              </Button>
              <Button
                variant="outline"
                size="sm"
                disabled={page >= Math.ceil(pagination.total / pagination.limit)}
                onClick={() => setPage(page + 1)}
              >
                Next <ChevronRight className="w-4 h-4 ml-1" />
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Add Student Modal */}
      <Dialog open={addModalOpen} onOpenChange={setAddModalOpen}>
        <DialogContent className="sm:max-w-[600px]">
          <DialogHeader>
            <DialogTitle>Add New Student</DialogTitle>
          </DialogHeader>
          <div className="grid gap-2">
            {Object.keys(formData).map((key) => (
              <div key={key} className="flex flex-col">
                <Label>{key}</Label>
                <Input
                  name={key}
                  value={formData[key]}
                  onChange={handleInputChange}
                />
              </div>
            ))}
          </div>
          <DialogFooter>
            <Button onClick={handleAddStudent} disabled={loading}>
              Add Student
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Edit Student Modal */}
      <Dialog open={editModalOpen} onOpenChange={setEditModalOpen}>
        <DialogContent className="sm:max-w-[600px]">
          <DialogHeader>
            <DialogTitle>Edit Student</DialogTitle>
          </DialogHeader>
          <div className="grid gap-2">
            {Object.keys(formData).map((key) => (
              <div key={key} className="flex flex-col">
                <Label>{key}</Label>
                <Input
                  name={key}
                  value={formData[key]}
                  onChange={handleInputChange}
                />
              </div>
            ))}
          </div>
          <DialogFooter>
            <Button onClick={handleEditStudent} disabled={loading}>
              Update Student
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
